import React from "react";
import { Modal, Button } from "react-bootstrap";

const ProductDetailModal = ({ show, handleClose, product, addToCart }) => {
  if (!product) return null;

  const handleAddToCart = () => {
    addToCart(product);
    handleClose();
  };

  return (
    <Modal show={show} onHide={handleClose} centered>
      <Modal.Header closeButton></Modal.Header>
      <Modal.Body>
        <div className="text-center mb-3">
          <img
            src={product.image}
            alt={product.name}
            className="img-fluid rounded"
            style={{ maxHeight: "200px" }}
          />
        </div>
        <Modal.Title className="fw-bold">{product.name}</Modal.Title>
        <p>
          <strong>Price:</strong> {product.price}
        </p>
        <p>
          <strong>Quantity Remaining:</strong> {product.quantity}
        </p>
        <h5 className="mt-4">Product Information:</h5>
        <p>{product.description || "No description available."}</p>
      </Modal.Body>
      <Modal.Footer>
        {/* <Button variant="secondary" onClick={handleClose}>
          Close
        </Button> */}
        <Button
          variant="primary"
          onClick={handleAddToCart}
          disabled={product.quantity === 0}
        >
          Add to Cart
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default ProductDetailModal;
