import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "./ProductList.css";

const ProductList = ({ products, addToCart, onProductClick }) => {
  return (
    <div className="row row-cols-1 row-cols-sm-2 row-cols-md-3 row-cols-lg-4 g-4 hover">
      {products.map((product) => (
        <div key={product.id} className="col pr">
          <div
            className="card h-100 rounded-4 shadow-sm"
            onClick={() => onProductClick(product)}
          >
            <img
              src={product.image}
              className="card-img-top rounded-top-4 product-image"
              alt={product.name}
            />
            <div className="card-body d-flex flex-column">
              <h5 className="card-title fw-bold">{product.name}</h5>
              <p className="card-text">Price: {product.price}</p>
              <p className="card-text">Quantity: {product.quantity}</p>
              <button
                className="btn btn-primary mt-auto rounded-3"
                onClick={(e) => {
                  e.stopPropagation();
                  addToCart(product);
                }}
                disabled={product.quantity === 0}
              >
                Add to Cart
              </button>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
};

export default ProductList;
